<template>
  <div class="indexPage">
      <el-select v-model="values" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item"
            :label="item"
            :value="item">
          </el-option>
      </el-select>

      <div  class="container">
        <div v-for="item in list" :key="item._id" class="items">
          <img :src="item.imgUrl" alt="">
          <p>{{item.title + '    ' + item.price / 1000000000000000000 + 'KETH'}}</p>
        </div>
      </div>
      
  </div>
</template>

<script>
export default {
  name: 'IndexPage',
  data () {
    return {
      list:[],
      options:['全部'],
      values:'',
      oldList:[],
    }
  },
  watch:{
    values(val){
      this.fitters(val)
    }
  },
  methods: {
    fitters(value){
      this.list = this.oldList
      if(value == '全部'){
        return
      }else {
        let newList = this.list.filter(val=>{
          return val.title == value
        })
        this.list = newList
      }
      
    },
    fetch(){
      this.Interface.getData('get',this.Interface.news.list).then(val=>{
        if(val.status === 200){
          this.list = val.data.order_list
          this.list.sort((a,b)=> {
            return b.price - a.price
          })
          this.oldList = this.list;
          this.list.map(val=>{
            let flag =this.options.indexOf(val.title)
              if(flag == -1){ 
                this.options.push(val.title)
              }
          })
        }
      })
    },
  },
  mounted(){
     this.fetch()
  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import './IndexPage.scss';
</style>



