import Vue from 'vue'
import axios from 'axios'
import AxiosConfig from './axios-config'
// import VueAxios from 'vue-axios'
import wx from 'weixin-js-sdk';
import qs from 'qs';
import wxConfig from './wx-config'

let AxiosSND = axios.create(AxiosConfig);
// axios拦截器 判断超时
AxiosSND.interceptors.response.use((res) => {
  return res;
}, (error) => {
  if (error.response) {
    return Promise.resolve(error.response);
  } else if (error.request) {
    Vue.prototype.$message.error('网络异常，请检查您的网络并刷新重试！');
    return Promise.reject(error.request);
  } else {
    Vue.prototype.$message.error('未知错误，请刷新重试！');
    return Promise.reject(error.message);
  }
});
Object.defineProperties(Vue.prototype, {
  axios: {
    get() {
      return AxiosSND
    }
  },
  $http: {
    get() {
      return AxiosSND
    }
  },
  wx: {
    get() {
      return wx
    }
  }
});

let toolkit = {
  news:{
    list: 'orders?page=1&rows=999&sortType=default&sortOrder=1&types=KethKnight'
  },
  vote:{
    post:'/vote'//news_id token name tel company position
  },
  feedback:{
    post:'/feedback'
  },
  // 上传企业内容
  upload:{
    company: '/enterprise',
    post: '/upload'
  },
  get:{
    company: '/enterprise/mine'
  },
  //检测微信支付是否成功
  apply: {
    check: '/wechat/checkPayment',
    pay: '/wechat/webPay'
  },
  // 深度clone一个对象
  cloneObj: function (obj) {
    let str, newobj = obj.constructor === Array ? [] : {};
    if (typeof obj !== 'object') {
      return;
    } else if (window.JSON) {
      str = JSON.stringify(obj), //系列化对象
        newobj = JSON.parse(str); //还原
    } else {
      for (let i in obj) {
        newobj[i] = typeof obj[i] === 'object' ?
          cloneObj(obj[i]) : obj[i];
      }
    }
    return newobj;
  },
  // 在location中获得指定参数的值，注意传入参数名称要和location中的参数名称相同
  GetQueryUrl: (url, name) => {
    let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    let r = ('?' + url.split('?')[1]).substr(1).match(reg);
    if (r != null) {
      return unescape(r[2]);
    } else {
      return "";
    }
  },
  //时间格式化
  getTime: function (time, formatType) {
    var newDate = new Date();
    newDate.setTime(time * 1000);
    Date.prototype.format = function (format) {
      var date = {
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "h+": this.getHours(),
        "m+": this.getMinutes()
      };
      if (/(y+)/i.test(format)) {
        format = format.replace(RegExp.$1, (this.getFullYear() + '').substr(4 -
          RegExp.$1.length));
      }
      for (var k in date) {
        if (new RegExp("(" + k + ")").test(format)) {
          format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? date[k] : (
            "00" + date[k]).substr(("" + date[k]).length));
        }
      }
      return format;
    }
    return newDate.format(formatType);
  },
  // 时间特殊格式
  timeFormat: (dateStr) => {
    var publishTime = dateStr,
      d_seconds,
      d_minutes,
      d_hours,
      d_days,
      timeNow = parseInt(new Date().getTime() / 1000),
      d,

      date = new Date(publishTime * 1000),
      Y = date.getFullYear(),
      M = date.getMonth() + 1,
      D = date.getDate(),
      H = date.getHours(),
      m = date.getMinutes(),
      s = date.getSeconds();
    //小于10的在前面补0
    if (M < 10) {
      M = '0' + M;
    }
    if (D < 10) {
      D = '0' + D;
    }
    if (H < 10) {
      H = '0' + H;
    }
    if (m < 10) {
      m = '0' + m;
    }
    if (s < 10) {
      s = '0' + s;
    }

    d = timeNow - publishTime;
    d_days = parseInt(d / 86400);
    d_hours = parseInt(d / 3600);
    d_minutes = parseInt(d / 60);
    d_seconds = parseInt(d);

    if (d_days > 0 && d_days < 3) {
      return d_days + '天前';
    } else if (d_days <= 0 && d_hours > 0) {
      return d_hours + '小时前';
    } else if (d_hours <= 0 && d_minutes > 0) {
      return d_minutes + '分钟前';
    } else if (d_seconds < 60) {
      if (d_seconds <= 0) {
        return '刚刚发表';
      } else {
        return d_seconds + '秒前';
      }
    } else if (d_days >= 3 && d_days < 30) {
      return M + '-' + D + ' ' + H + ':' + m;
    } else if (d_days >= 30) {
      return Y + '-' + M + '-' + D + ' ' + H + ':' + m;
    }
  },
  // 获得数据
  getData: (type, url, params) => {
    let axios;
    if ('get' == type) {
      axios = AxiosSND.get(url, {
        params: params
      })
    } else if ('post' == type) {
      axios = AxiosSND.post(url, qs.stringify(params));
    }
    let promise = new Promise((resolve, reject) => {
      axios.then(response => {
        if (200 == response.status) {
          resolve(response);
        } else if (401 == response.status) {
          Vue.prototype.$message.error('未登录');
          // location.href = toolkit.url + 'error.html';
        } else if (500 == response.status) {
          Vue.prototype.$message.error('接口出错了');
          // location.href = toolkit.url + 'error.html';
        } else if (404 == response.status || 405 == response.status) {
          Vue.prototype.$message.error('接口未找到');
        } else if (403 == response.status) {
          Vue.prototype.$message.error('token不存在');
        } else if (422 == response.status) {
          Vue.prototype.$message.error('参数缺失，请确认参数');
        } else {
          console.log('other');
          // location.href = toolkit.url + 'error.html';
        }
      }).catch(error => {
        if (error.response) {
          resolve(error.response);
          console.log(error.response, 'error response');
        } else if (error.request) {
          resolve(error.request);
          console.log(error.request, 'error request');
        } else {
          resolve(error.message);
          console.log(error.message, 'error message');
        }
      })
    });
    return promise;
  },
  // 分享函数
  shareFuc: (shareData) => {
    let jsApiList = [
      'onMenuShareAppMessage',
      'onMenuShareTimeline',
      'onMenuShareQQ',
      'onMenuShareWeibo',
      'onMenuShareQZone',
    ];
    wxConfig.wxConfig(jsApiList);
    wx.ready(() => {
      wx.onMenuShareAppMessage(shareData);
      wx.onMenuShareTimeline(shareData);
      wx.onMenuShareQQ(shareData);
      wx.onMenuShareWeibo(shareData);
      wx.onMenuShareQZone(shareData);
    })
  },
  // 默认的分享
  defaultShare: (url) => {
    let shareData = {
      title: '会展时间&大事|“2017年中国会展业十大新闻”请你来选！', // 分享标题
      desc: '中国经济网、中国贸易报 共同发起', // 分享描述
      link: encodeURI('http://vote-api.qeebu.com/wechat/share?return_url=' + url), // 分享链接
      imgUrl: location.origin + '/static/shareImg.png'
    };
    toolkit.shareFuc(shareData);
  },
  // 获得返回的token
  getToken: () => {
    if (location.href.includes('&')) {
      return location.hash.split('?')[1].split('&')[0].split('=')[1];
    } else {
      return location.hash.split('?')[1].split('=')[1];
    }
  },
  // hidden
  hideShare: () => {
    let jsApiList = [
      'hideMenuItems',
      'hideOptionMenu'
    ];
    wxConfig.wxConfig(jsApiList);
    wx.ready(() => {
      wx.hideOptionMenu();
      wx.hideMenuItems({
        menuList: [
          "menuItem:share:appMessage",
          "menuItem:share:timeline"
        ]
      });
    })
  },
  // 微信支付
  wechatPay: (jsApi, orderNum, routerUrl) => {
    let timer = null;
    let param = {
      order_trade_num: orderNum
    }
    timer = setInterval(() => {
      toolkit.getData('get', toolkit.apply.check, param).then(res => {
        if (200 == res.data.code) {
          clearInterval(timer);
          if (routerUrl) {
            Vue.$router.push(routerUrl);
          }
        }
      })
    }, 1000);
    WeixinJSBridge.invoke('getBrandWCPayRequest', jsApi, (res) => {
      // console.log(res.errMsg, "wxres")
      if (res.err_msg == 'get_brand_wcpay_request:ok') {
        toolkit.getData('get', toolkit.apply.check, param).then(res => {
          if (200 == res.data.code) {
            clearInterval(timer);
            timer = null;
            if (routerUrl) {
              Vue.$router.push(routerUrl);
            }
          }
        })
      } else {
      }
      /*timer = setInterval(() => {
        toolkit.getData('get', this.apply.check, param).then(res => {
          if (200 == res.data.code) {
            clearInterval(timer);
            alert("支付成功！")
          }
        })
      }, 1000)*/
    })
  }
};

// 判断以及获得token
let checkToken = () => {
  if (location.hash && location.hostname != 'localhost') {
    if (location.hash.split('?')[1] && location.hash.split('?')[1].split('=')[0] == 'token') {
      sessionStorage.setItem('token', toolkit.getToken());
    } else {
      location.href = 'http://vote-api.qeebu.com/wechat/index?return_url=' + location.hash.split('#')[1].substr(1);
    }
  } else if (location.hostname == 'localhost') {

  } else {
    location.href = 'http://vote-api.qeebu.com/wechat/index?return_url=';
  }
};

checkToken();

// 获得用户信息
let getUserInfo = () => {
  let token = '';
  if (location.hostname == 'localhost') {
    token = '0c63445df36caede827dbbc0f6ca0701';
  } else {
    if (sessionStorage.getItem('token')) {
      token = sessionStorage.getItem('token');
    } else {
      checkToken();
    }
  }
  toolkit.getData('post', toolkit.user.getUserInfo, {
    token: token
  }).then(response => {
    if (200 == response.data.code) {
      sessionStorage.setItem('user_id', response.data.data.id);
    } else {
      Vue.prototype.$message.error('获取用户信息失败，请重新登陆');
    }
  })
};

// getUserInfo();

export default toolkit



