import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage'
import about from './subRouter/about'
import blog from './subRouter/blog'
import index from './subRouter/index'
import project from './subRouter/project'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage,
      redirect: '/Index',
      children: [
        ...about,
        ...blog,
        ...index,
        ...project,
      ]
    }
  ]
})



