import Vue from 'vue';
import Interface from './interface';

function wxConfig(jsApiList) {
  Interface.getData('get', '/wechat/sign', {
    url: encodeURI('http://vote-wap.qeebu.com/')
  }).then(response => {
    let configObj = response.data.data;
    configObj.debug = false;
    configObj.jsApiList = jsApiList;
    Vue.prototype.wx.config(configObj);
  })
}

export default {wxConfig};



