import ClassifyPage from '@/components/blog/ClassifyPage.vue'
import DetailPage from '@/components/blog/DetailPage.vue'

const routes = [
    {
        path:'/Classify',
        name:'ClassifyPage',
        component:ClassifyPage
    },{
        path:'/Detail:id',
        name:'DetailPage',
        component:DetailPage
    }
]

export default routes ;


